@import "../vendor/swiper-bundle.min.css";

/* Events Swiper styles */
/* ------------------------------ */
.events .swiper-wrapper {
  max-height: 250px;
  margin-bottom: 2rem;
}

.events .swiper-slide {
  background-color: #E6F1EA;
  height: auto;
  box-sizing: border-box;
  padding: 20px;
  transition: background-color .33s ease-in-out;
}

.events .swiper-slide a {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 100%;
  text-decoration: none;
  transition: color 3s ease-in-out;
}

.swiper-pagination-bullet {
  width: 7px;
  height: 7px;
  text-align: center;
  line-height: 20px;
  font-size: 12px;
  color: #000;
  opacity: 1;
  background: rgba(0, 0, 0, 0.2);
}

.swiper-pagination-bullet-active {
  color: #fff;
  background: rgb(31, 37, 41);
}

/* Headings and Time Styles */
/* ------------------------------ */
h2,
h3,
time {
  font-weight: 400;
  margin: 0;
  padding: 0;
  color: rgb(34, 30, 30);
}

h2 {
  font-size: clamp(16px, 5vw, 24px);
  overflow-wrap: break-word;
  margin-bottom: 1rem;
}

h3 {
  font-family: 'Trade Gothic';
  text-transform: uppercase;
  color: gray;
  font-size: clamp(15px, 1vw, 16px);
  height: 30px;
  margin-bottom: 1rem;
}

time {
  font-size: clamp(12px, 5vw, 16px);
  margin-top: auto;
}

/* People */
.people .swiper .swiper-wrapper {
  height: 100%;
  margin-bottom: 2rem;
}

.people .swiper img {
  width: auto;
}

.people .swiper-slide {
  width: auto !important;
}


.people {
  position: relative;
  overflow: hidden;
  /* This ensures that the gradient does not extend beyond the container */
}

.people::before,
.people::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 10px;
  /* Adjust the width of the gradient as needed */
  z-index: 10;
  /* Ensure the gradient is above the content */
  pointer-events: none;
  /* Prevents the gradient from interfering with interaction */
}

.people::before {
  left: 0;
  background: linear-gradient(to right, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));
}

.people::after {
  right: 0;
  background: linear-gradient(to left, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));
}

h3.title {
  margin-top: .2rem;
  margin-bottom: 1.3rem;
  padding: 0 !important;
  height: 0;
  font-size: 16px;
  color: black;
}

/* swiper navigation overrides */
.swiper-button-next:after,
.swiper-button-prev:after {
  content: '' !important;
}

.swiper-home-hero .swiper-button-prev,
.swiper-home-hero .swiper-button-next {
  position: absolute;
  top: 0;
  height: 100vh;
  width: 50%;
  z-index: 1;
  margin: 1px !important;
  padding: 1px !important;
}

.swiper-home-hero .swiper-button-prev {
  cursor: url('../../images/arrow_carousel_left.png') 10 15, auto;
  left: 0;
}

.swiper-home-hero .swiper-button-next {
  cursor: url('../../images/arrow_carousel_right.png') 10 15, auto;
  right: 0;
}

.swiper-home-hero .metadata {
  padding: 1rem 0;
}

.swiper-home-hero time {
  font-family: 'Trade Gothic';
  text-transform: uppercase;
  color: gray;
  font-size: clamp(15px, 1vw, 16px);
  margin-bottom: 1rem;
}

.swiper-home-hero h2 {
  height: 0;
  font-size: 16px;
  color: black;
  font-size: clamp(1rem, 10vw, 2rem);
}

.swiper-home-hero .swiper-slide {
  padding-bottom: clamp(2rem, 3.5rem, 30vw);
}

.swiper-container-horizontal>.swiper-pagination {
  position: absolute;
  bottom: 0;
}

.swiper-home-hero .metadata {
  padding: 1rem 0;
}
